body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  overflow-x: hidden;
  height: 100%;
}

.wrapper {
    display: flex;
    justify-content: center;
    min-height: 100vh;
}

.App {
    width: 650px;
    touch-action: manipulation;
    -webkit-user-select: none;
            user-select: none;
}

.side-panel {
    flex-grow: 1;
    background-color: #d8d4d4;
}

h1 {
    font-size: 1.5rem;
    color: #6f7172;
    text-align: center;
    font-family: 'Oswald';
    font-weight: bold;
    letter-spacing: 1px;
}

p {
    color: #6f7172;
    font-family: 'Oswald';
}
.bar {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.icon {
    color: #6f7172;
    height: 32px;
    /* flex-grow: 1; */
}

.bar a {
    flex-grow: 1;
    text-decoration: none;
}

.bar .title {
    display: flex;
    justify-content: center;
    font-size: 2.5rem;
    flex-grow: 3;
    letter-spacing: 1px;
    width: -webkit-min-content;
    width: min-content;
    /* border: 1px solid red; */
}

.tile {
    background-color: #edeff1;
    height: 120px;
    width: 120px;
    border-radius: 12%;
    border-bottom: 7px solid rgba(0,0,0,.1);

    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    font-size: .9rem;
    letter-spacing: .5px;
    margin: 4px;
    /* font-weight: 600; */
    
    font-family: 'Oswald';
    text-transform: uppercase;
    cursor: pointer;

}

.red {
    background-color: #ff3c28;
    /* color: white; */
}
.blue {
    background-color: #0ab9e6;
    /* color: white; */
}
.purple {
    background-color: #b400e6;
    /* color: white; */
}
.orange {
    background-color: #faa005;
    /* color: white; */
}
.green {
    background-color: #1edc00;
    /* color: white; */
}
.pink {
    background-color: #ff3278;
    /* color: white; */
}

.highlight{
    box-shadow: rgb(233, 186, 58) 0px 5px 10px;
}

#board {
    /* touch-action: manipulation; */
    display: flex;
    flex-direction: column;
}

#row {
    display: flex;
    flex-direction: row;
}

#groups-and-swaps{
    display: flex;
    justify-content: space-between;
    margin: 10px;
}
